h1{
  text-align:center;
  font-weight: 300;
}

/* Walk cycle sprite image from: http://blog.nanchu.me */
#animation{
  margin:0 auto; /* for demo purpose only */
  width: 83.33333333333333px; /* the total width of the sprite image divided by each frame width */
	height: 126px;
background:url(https://38.media.tumblr.com/tumblr_mb4zsyUYCT1rvuv0d.png);
		-webkit-animation: walk-cycle 1.0s steps(6) infinite;
		        animation: walk-cycle 1.0s steps(6) infinite; /* the sprite has 6 frames hence: steps(6) */
}

/* Walk cycle animation */
	@-webkit-keyframes walk-cycle{
      0% {
        background-position: 0px;
      }
      100% {
        background-position: -500px; /* loop the animation frames once the end of the image is reached, the total width of the sprite image is 500px  */
      }
  }
	@keyframes walk-cycle{
      0% {
        background-position: 0px;
      }
      100% {
        background-position: -500px; /* loop the animation frames once the end of the image is reached, the total width of the sprite image is 500px  */
      }
  }
